import * as React from "react";
import { alpha, styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Link from "@mui/material/Link";
import { Link as RouterLink, useLocation } from "react-router-dom";
import Stack from "@mui/material/Stack";
import IconButton from "@mui/material/IconButton";
import { SvgIcon, Typography } from "@mui/material";
import Divider from "@mui/material/Divider";
import SchoolIcon from "@mui/icons-material/School";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import BlueSkyLogo from "./BlueSkyLogo";
import Sitemark from "./SitemarkIcon";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import useMediaQuery from "@mui/material/useMediaQuery";
import MenuIcon from "@mui/icons-material/Menu";

// Keep your styled Toolbar as before
const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  flexShrink: 0,
  borderRadius: `calc(${theme?.shape?.borderRadius}px + 8px)`,
  backdropFilter: "blur(24px)",
  border: "1px solid",
  borderColor: theme.palette.divider,
  backgroundColor: alpha(theme.palette.background.default, 0.4),
  boxShadow: theme.shadows[1],
  padding: "8px 12px",
  overflow: "hidden",
}));

// Optional helper if you still need Link with Router
export const linkWithRouter = React.forwardRef((props: any, ref) => (
  <Link {...props} component={RouterLink} ref={ref} />
));

export default function AppAppBar() {
  const theme = useTheme();
  const location = useLocation();
  const onHomePage = location.pathname === "/";

  const isMobile = useMediaQuery("(max-width:800px)");
  // Progressive collapsing thresholds (wide  narrow)
  const collapseIcons = useMediaQuery("(max-width:800px)");
  const collapseContact = useMediaQuery("(max-width:600px)");
  const collapseTeaching = useMediaQuery("(max-width:400px)");
  const collapsePublications = useMediaQuery("(max-width:300px)");
  const collapseResearch = useMediaQuery("(max-width:200px)");
  const collapseCV = useMediaQuery("(max-width:100px)");

  const hamburgerActive =
    collapseIcons ||
    collapseContact ||
    collapseTeaching ||
    collapsePublications ||
    collapseResearch ||
    collapseCV;

  // Hamburger menu anchor
  const [socialMenuAnchor, setSocialMenuAnchor] =
    React.useState<null | HTMLElement>(null);
  const openSocialMenu = Boolean(socialMenuAnchor);
  const handleOpenSocialMenu = (event: React.MouseEvent<HTMLElement>) => {
    setSocialMenuAnchor(event.currentTarget);
  };
  const handleCloseSocialMenu = () => {
    setSocialMenuAnchor(null);
  };

  // Contact dropdown as Menu
  const [contactAnchorEl, setContactAnchorEl] =
    React.useState<null | HTMLElement>(null);
  const openContactMenu = Boolean(contactAnchorEl);
  const handleOpenContactMenu = (event: React.MouseEvent<HTMLElement>) => {
    setContactAnchorEl(event.currentTarget);
  };
  const handleCloseContactMenu = () => setContactAnchorEl(null);

  return (
    <AppBar
      position="fixed"
      enableColorOnDark
      sx={{
        boxShadow: 0,
        bgcolor: "transparent",
        backgroundImage: "none",
        mt: "calc(var(--template-frame-height, 0px) + 28px)",
      }}
    >
      <Container maxWidth={"lg"}>
        <StyledToolbar variant="dense" disableGutters>
          <Box
            sx={{ flexGrow: 1, display: "flex", alignItems: "center", px: 0 }}
          >
            <Sitemark />
            <Box sx={{ display: "flex", alignItems: "center" }}>
              {!onHomePage && (
                <Button
                  variant="text"
                  color="info"
                  size="small"
                  component={linkWithRouter}
                  to="/"
                >
                  Home
                </Button>
              )}
              <Button
                variant="text"
                color="info"
                size="small"
                component={linkWithRouter}
                to="/cv"
              >
                CV
              </Button>

              {!collapseResearch && (
                <Button
                  variant="text"
                  color="info"
                  size="small"
                  component={linkWithRouter}
                  to="/research"
                >
                  Research
                </Button>
              )}

              {!collapsePublications && (
                <Button
                  variant="text"
                  color="info"
                  size="small"
                  component={linkWithRouter}
                  to="/publications"
                >
                  Publications
                </Button>
              )}

              {!collapseTeaching && (
                <Button
                  variant="text"
                  color="info"
                  size="small"
                  component={linkWithRouter}
                  to="/teaching"
                >
                  Teaching
                </Button>
              )}
              {!collapseContact && (
                <Button
                  variant="text"
                  color="info"
                  size="small"
                  onClick={handleOpenContactMenu}
                >
                  Contact
                </Button>
              )}
              <Menu
                anchorEl={contactAnchorEl}
                open={openContactMenu}
                onClose={handleCloseContactMenu}
                anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
                transformOrigin={{ vertical: "top", horizontal: "left" }}
              >
                <MenuItem
                  component="a"
                  href="mailto:tliu20@albany.edu"
                  onClick={handleCloseContactMenu}
                >
                  <ListItemText primary="Professional: tliu20@albany.edu" />
                </MenuItem>
                <MenuItem
                  component="a"
                  href="mailto:liutingting0513@gmail.com"
                  onClick={handleCloseContactMenu}
                >
                  <ListItemText primary="Personal: liutingting0513@gmail.com" />
                </MenuItem>
              </Menu>
            </Box>
          </Box>

          <Box sx={{ display: "flex", gap: 1, alignItems: "center" }}>
            {!isMobile && (
              <Stack
                direction="row"
                spacing={1}
                useFlexGap
                sx={{ justifyContent: "left", color: "text.secondary" }}
              >
                <IconButton
                  color="inherit"
                  size="small"
                  href="https://scholar.google.com/citations?user=27bJMq8AAAAJ"
                  aria-label="Google Scholar"
                  sx={{ alignSelf: "center" }}
                  title="Google Scholar"
                >
                  <SchoolIcon />
                </IconButton>
                <IconButton
                  color="inherit"
                  size="small"
                  href="https://www.linkedin.com/in/qing-tingting-liu-251bb6181/"
                  aria-label="LinkedIn"
                  sx={{ alignSelf: "center" }}
                  title="LinkedIn"
                >
                  <LinkedInIcon />
                </IconButton>
                <IconButton
                  color="inherit"
                  size="small"
                  href="https://bsky.app/profile/bardjester.bsky.social"
                  aria-label="BlueSky"
                  sx={{ alignSelf: "center" }}
                  title="BlueSky"
                >
                  <SvgIcon>
                    <BlueSkyLogo />
                  </SvgIcon>
                </IconButton>
                <IconButton
                  color="inherit"
                  size="small"
                  href="https://www.facebook.com/profile.php?id=100005252685126"
                  aria-label="Facebook"
                  sx={{ alignSelf: "center" }}
                  title="Facebook"
                >
                  <FacebookIcon />
                </IconButton>
                <IconButton
                  color="inherit"
                  size="small"
                  href="https://x.com/bard_jester"
                  aria-label="X"
                  sx={{ alignSelf: "center" }}
                  title="X"
                >
                  <TwitterIcon />
                </IconButton>
              </Stack>
            )}
            {hamburgerActive && (
              <>
                <IconButton
                  color="inherit"
                  size="small"
                  aria-label="Open menu"
                  onClick={handleOpenSocialMenu}
                  title="Menu"
                >
                  <MenuIcon />
                </IconButton>
                <Menu
                  anchorEl={socialMenuAnchor}
                  open={openSocialMenu}
                  onClose={handleCloseSocialMenu}
                  anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
                  transformOrigin={{ vertical: "top", horizontal: "right" }}
                >
                  {collapseContact && (
                    <MenuItem
                      onClick={(e) => {
                        handleCloseSocialMenu();
                        handleOpenContactMenu(e);
                      }}
                    >
                      <ListItemText primary="Contact" />
                    </MenuItem>
                  )}
                  {collapsePublications && (
                    <MenuItem
                      component={linkWithRouter}
                      to="/publications"
                      onClick={handleCloseSocialMenu}
                    >
                      <ListItemText primary="Publications" />
                    </MenuItem>
                  )}
                  {collapseResearch && (
                    <MenuItem
                      component={linkWithRouter}
                      to="/research"
                      onClick={handleCloseSocialMenu}
                    >
                      <ListItemText primary="Research" />
                    </MenuItem>
                  )}
                  {collapseTeaching && (
                    <MenuItem
                      component={linkWithRouter}
                      to="/teaching"
                      onClick={handleCloseSocialMenu}
                    >
                      <ListItemText primary="Teaching" />
                    </MenuItem>
                  )}
                  {isMobile &&
                    (collapseContact ||
                      collapsePublications ||
                      collapseResearch) && <Divider />}
                  {isMobile && (
                    <>
                      <MenuItem
                        component="a"
                        href="https://scholar.google.com/citations?user=27bJMq8AAAAJ"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={handleCloseSocialMenu}
                      >
                        <ListItemIcon>
                          <SchoolIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText primary="Google Scholar" />
                      </MenuItem>
                      <MenuItem
                        component="a"
                        href="https://www.linkedin.com/in/qing-tingting-liu-251bb6181/"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={handleCloseSocialMenu}
                      >
                        <ListItemIcon>
                          <LinkedInIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText primary="LinkedIn" />
                      </MenuItem>
                      <MenuItem
                        component="a"
                        href="https://bsky.app/profile/bardjester.bsky.social"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={handleCloseSocialMenu}
                      >
                        <ListItemIcon>
                          <SvgIcon fontSize="small">
                            <BlueSkyLogo />
                          </SvgIcon>
                        </ListItemIcon>
                        <ListItemText primary="BlueSky" />
                      </MenuItem>
                      <MenuItem
                        component="a"
                        href="https://www.facebook.com/profile.php?id=100005252685126"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={handleCloseSocialMenu}
                      >
                        <ListItemIcon>
                          <FacebookIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText primary="Facebook" />
                      </MenuItem>
                      <MenuItem
                        component="a"
                        href="https://x.com/bard_jester"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={handleCloseSocialMenu}
                      >
                        <ListItemIcon>
                          <TwitterIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText primary="X (Twitter)" />
                      </MenuItem>
                    </>
                  )}
                </Menu>
              </>
            )}
          </Box>
        </StyledToolbar>
      </Container>
    </AppBar>
  );
}
